<template>
<div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
        <!-- Button trigger modal -->
        <button class="btn btn-primary" @click="openModal(true)"></button>
    </div>
    <table class="table mt-4">
        <thead>
            <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="item in coupons" :key="item.id">
                <td>{{ item.title }}</td>
                <td>{{ item.percent }}%</td>
                <td>{{ item.due_date | date}}</td>
                <td>
                    <span v-if="item.is_enabled" class="text-success"></span>
                    <span v-else class="text-danger"></span>
                </td>
                <td>
                    <div class="btn-group" role="group" aria-label="Basic example">
                        <button class="btn btn-sm btn-outline-primary" @click="openModal(false, item)"></button>
                        <button class="btn btn-sm btn-outline-danger" @click="openDelModal(item)"></button>
                    </div>
                </td>
            </tr>
        </tbody>
    </table>
    <!--  -->
    <pagination :pages="pagination" @changedpage="getCoupons"></pagination>
    <!-- / Modal -->
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-lg" role="document">
            <div class="modal-content border-0">
                <div class="modal-header bg-dark text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                        <span></span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="form-group">
                        <label for="title"></label>
                        <input type="text" class="form-control" id="title" v-model="tempCoupon.title" placeholder="" />
                    </div>
                    <div class="form-group">
                        <label for="couponCode"></label>
                        <input type="text" class="form-control" id="couponCode" v-model="tempCoupon.code" placeholder="" />
                    </div>
                    <div class="form-group">
                        <label for="dueDate"></label>
                        <input type="date" class="form-control" id="dueDate" v-model="due_date" />
                    </div>
                    <div class="form-group">
                        <label for="percent"></label>
                        <input type="number" class="form-control" id="percent" v-model="tempCoupon.percent" placeholder="" />
                    </div>
                    <div class="form-group">
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" :true-value="1" :false-value="0" v-model="tempCoupon.is_enabled" id="is_enabled" />
                            <label class="form-check-label" for="is_enabled"></label>
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-primary" @click="updateCoupon"></button>
                </div>
            </div>
        </div>
    </div>
    <!--  Modal" -->
    <div class="modal fade" id="delCouponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                        <span></span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    
                    <strong class="text-danger">{{ tempCoupon.title }}</strong> 
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-danger" @click="deleteCoupon"></button>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import $ from "jquery";
import Pagination from "../views/Pagination";
export default {
    data() {
        return {
            coupons: [],
            tempCoupon: {},
            isNew: false,
            isLoading: false,
            pagination: {},
            // 
            //  JavaScript new Date() 
            due_date: new Date(), // Wed Jan 15 2020 16:01:52 GMT+0800 ()
        };
    },
    components: {
        Pagination
    },
    watch: {
        due_date() {
            const vm = this;
            // vm.due_date  "2020-01-15" 
            //  new Date("2020-01-15")  1579046400000 ()
            //  Math.floor(new Date("2020-01-15") / 1000)  ()
            const timestamp = Math.floor(new Date(vm.due_date) / 1000); // 1579046400
            vm.tempCoupon.due_date = timestamp; //  timestamp  tempCoupon.due_date
        }
    },
    methods: {
        getCoupons(page = 1) {
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupons?page=${page}`;
            // console.log(process.env.VUE_APP_APIPATH, process.env.VUE_APP_CUSTOMPATH);
            vm.isLoading = true;
            this.$http.get(api).then(response => {
                vm.isLoading = false;
                vm.coupons = response.data.coupons;
                vm.pagination = response.data.pagination;
                console.log(response.data);
            });
        },
        openModal(isNew, item) {
            const vm = this;
            if (isNew) {
                vm.due_date = new Date(); //  Modal  bug
                this.tempCoupon = {};
                this.isNew = true;
            } else {
                this.tempCoupon = Object.assign({}, item); //  assign  tempCoupon
                this.isNew = false;
                // new Date(timestamp*1000) :  timestamp 
                // 2011-10-05T14:48:00.000Z =>  "T"  [0] 
                const dateAndTime = new Date(vm.tempCoupon.due_date * 1000)
                    .toISOString()
                    .split("T");
                vm.due_date = dateAndTime[0];
            }
            $("#couponModal").modal("show");
        },
        updateCoupon() {
            //  Coupon
            let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon`;
            const vm = this;
            //  Coupon
            let httpMethod = "post";
            if (!vm.isNew) {
                api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
                httpMethod = "put";
                //  vm.due_date  "Wed Jan 15 2020 16:01:52 GMT+0800 ()" 
                // vm.due_date = new Date(vm.tempCoupon.due_date * 1000); // 
            }
            // 
            console.log(process.env.VUE_APP_APIPATH, process.env.VUE_APP_CUSTOMPATH);
            this.$http[httpMethod](api, {
                data: vm.tempCoupon
            }).then(response => {
                console.log(response.data);
                if (response.data.success) {
                    $("#couponModal").modal("hide");
                    vm.getCoupons();
                } else {
                    $("#couponModal").modal("hide");
                    vm.getCoupons();
                    console.log("");
                }
            });
        },
        openDelModal(item) {
            this.tempCoupon = item;
            $("#delCouponModal").modal("show");
        },
        deleteCoupon() {
            const vm = this;
            const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
            console.log(process.env.VUE_APP_APIPATH, process.env.VUE_APP_CUSTOMPATH);
            this.$http.delete(api).then(response => {
                console.log(response.data);
                if (response.data.success) {
                    $("#delCouponModal").modal("hide");
                    vm.getCoupons();
                } else {
                    $("#delCouponModal").modal("hide");
                    vm.getCoupons();
                    console.log("");
                }
            });
        }
    },
    created() {
        this.getCoupons(); // init
    }
};
</script>
